.form {
  width: 360px;
  height: 480px;
  display: flex;
  flex-direction: column;
  padding: 40px 120px 90px 0px;
  z-index: 16;

  .error {
    color: red;
    font-size: 0.8em;
    margin: 0 30px;
  }

  &-login {
    &.text1 {
      margin-top: 15px;
      position: relative;
    }
    &.text2 {
      margin-top: 25px;
      position: relative;
      width: 350px;
      .identificacion {
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
        height: 57px;
        border: 1px solid #5e6488;
      }
      input[type="text"] {
        width: 200px;
        height: 34px;
        padding-top: 20px;
        padding-left: 8px;
        border: 1px solid #5e6488;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        position: absolute;
      }

      label {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 12px;
        color: #999;
        pointer-events: none;
        transition: all 0.2s;
        padding: 5px;
      }

      input[type="text"]:focus + label,
      input[type="text"]:not(:placeholder-shown) + label {
        top: 0px;
        margin-left: 1vw;
        left: 5px;
        font-size: 12px;
        color: #000;
      }
    }
    &.text3 {
      margin-top: 16px;
      position: relative;
      width: auto;
      input[type="text"] {
        width: 350px;
        height: 34px;
        padding-top: 20px;
        padding-left: 8px;
        border: 1px solid #5e6488;
        border-radius: 8px;
      }

      label {
        top: 0;
        left: 0;
        font-size: 12px;
        color: #999;
        pointer-events: none;
        transition: all 0.2s;
        padding: 5px;
      }

      input[type="text"]:focus + label,
      input[type="text"]:not(:placeholder-shown) + label {
        position: absolute;
        top: 0px;
        left: 5px;
        border-radius: 10px;
        font-size: 12px;
        color: #000;
      }
    }
  }
}

.form-group {
  &__tittle {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &.sub {
      color: #000;
      background: linear-gradient(86.01deg, #00f4e2 0%, #00ff7f 100%);
      border-radius: 4px;
      padding: 4px 8px;
      font-size: 16px;
      font-weight: 700;
      letter-spacing: 0.4px;
      text-align: left;
    }
    &.principal {
      margin-top: 15px;
      max-width: 350px;
      font-size: 32px;
      font-weight: 700;
      line-height: 40px;
    }
  }
}

.label {
  text-align: justify;
}

.button {
  align-self: flex-start;
  background-color: #000;
  color: #fff;
  border: none;
  margin: 10px;
  padding: 8px;
  border-radius: 40px;
  width: 195px;
  height: 54px;
  cursor: pointer;
  gap: 8px;

  label {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0.4px;
    text-align: center;
  }
}

.checkboxes {
  display: flex;
  flex-direction: column;
  margin-top: 15px;

  &.checkbox1 {
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: flex-start;
    height: 20px;

    input[type="checkbox"] {
      margin-right: 5px;
      width: 20px;
    }

    label {
      justify-content: center;
      font-size: 12px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0.1px;
      text-align: left;
    }
  }
  &.checkbox2 {
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: flex-start;
    height: 20px;

    input[type="checkbox"] {
      margin-right: 12px;
    }

    label {
      justify-content: flex-start;
      font-size: 12px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0.1px;
      text-align: left;
      height: 20px;
    }
  }
  &.terminos {
    display: flex;
    align-items: flex-start;

    a {
      color: #000;
      text-decoration: underline;
      cursor: pointer;
      font-size: 12px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0.1px;
      text-align: left;
    }
  }
}
.error-message {
  color: red;
  font-size: 10px;
  margin: 0 30px;
}

.label__1 {
  left: 153px !important;
}

.checkbox1 input[type="checkbox"]::before,
.checkbox2 input[type="checkbox"]::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 1px solid black;
  background-color: white;
  text-align: center;
  border-radius: 5px;
}

.checkbox1 input[type="checkbox"]:checked::before,
.checkbox2 input[type="checkbox"]:checked::before {
  background-color: black;
  content: "\2713";
  font-size: 12px;
  color: white;
}

@media (max-width: 768px) {
  .form {
    padding: 0;
    &-group {
      &__tittle {
        max-width: 235px;
        margin-top: 1rem;
        &.principal {
          font-size: 28px;
          line-height: 36px;
        }
        &.sub {
          max-width: 188px;
          font-size: 13px;
          padding: 2px 8px;
        }
      }
    }
    &-login {
      margin-top: 2.5rem;
    }
    .button {
      width: 100%;
      margin-top: 35px;
    }
  }
}
